<template>
    <div>
        <div class="top-button">
            <el-radio-group v-model="radioFont" @change="changeFonts">
                <el-radio-button
                    v-for="(item, index) in fontNames"
                    :key="index" 
                    :label="item.fontName">
                        {{item.name}}
                </el-radio-button>
            </el-radio-group>
        </div>
        <div class="search">
            <div>
                <p class="search-item">
                    <el-input
                        placeholder="请输入要搜索的字体"
                        v-model="searchFont"
                        clearable
                        class="search-input"
                        @clear="initFonts">
                    </el-input>
                    <el-button type="primary" @click="searchFontsFn">搜索</el-button>
                </p>
                <p class="search-item">
                    <el-input
                        placeholder="请输入要转换编码"
                        v-model="realCode"
                        clearable
                        class="search-input"
                        @clear="clearRealCode">
                    </el-input>
                    <el-button type="primary" @click="realCodeFn">编码还原</el-button>
                </p>
                <p v-text="realCodeText"></p>
                <p class="search-item">
                    <el-input
                        placeholder="请输入要转换编码"
                        v-model="code"
                        clearable
                        class="search-input"
                        @clear="clearCode">
                    </el-input>
                    <el-button type="primary" @click="codeFn">编码</el-button>
                </p>
                <p v-html="codeText"></p>
            </div>
        </div>
        <div class="search-color-text" v-html="textObj"></div>
        <div class="font-list" id="font" :style="{ 'font-family': fontFamily }">
            <span 
                v-for="(font, index) in fontList" 
                :key="index"
                class="font-item-list">
                <span 
                    v-for="(f, i) in font"
                    :key="i"
                    class="font-item">
                    <span 
                        v-html="renderFonts(f)"
                        :class="[
                        'font-transfrom',
                        checkDefaultClass(f)
                        ]">
                    </span>
                    <span :class="checkDefaultClass(f)">{{ f }}</span>
                </span>
            </span>
        </div>
    </div>
</template>
<script>
import opentype from 'opentype.js'
import { getFontsList } from '../utils/index'
// import { createWorker, PSM, OEM } from 'tesseract.js';
// const worker = createWorker({
//   logger: m => console.log(m),
// });
export default {
    data(){
        return {
            fonts: [],
            excloudeFonts: ['.notdef', '.null', 'uni000D', 'space'],
            searchFont: '',
            fontList: {},
            textObj: '',
            radioFont: 'PingfangSC',
            fontFamily: 'longfor-font-MicrosoftYahei',
            realCode: '',
            code: '',
            realCodeText: '',
            codeText: '',
            fileList: [],
            fontNames: []
        }
    },
    methods: {
        async loadingFont(fontName){
            const fontPath = `fonts/${fontName}/${fontName}.ttf`
            // console.log(fontPath)
            return await opentype.load(fontPath)
        },
        async formatFonts(fontName){
            const fonts = await this.loadingFont(fontName)
            const fontObject = {}
            fonts.glyphNames.names
                .filter(item => {
                    return !this.excloudeFonts.includes(item)
                })
                .sort()
                .map(item => {
                    let fontUnicode
                    let realFontCode
                    if(item.indexOf('uni') === 0){
                        fontUnicode = item.slice(3)
                        realFontCode = item.slice(3, 7)
                    }else{
                        fontUnicode = item.slice(1)
                        realFontCode = item.slice(1, 5)
                    }
                    if(fontObject[realFontCode]){
                        fontObject[realFontCode].push(fontUnicode)
                    }else{
                        fontObject[realFontCode] = [fontUnicode]
                    }
                })
            this.fonts = fontObject
            this.initFonts()
        },
        checkDefaultClass(font){
            if(!font){
                return ''
            }
            const fontLastLetter = font.slice(4)
            return fontLastLetter == '3' ? 'default-font' : ''
        },
        renderFonts(font){
            return `&#x${font};`
        },
        searchFontsFn(){
            if(!this.searchFont){
                this.initFonts()
                return
            }
            const newFontObject = {}
            let textObj = ''
            this.searchFont
                .split('')
                .map(item => {
                    const unicode = (Number(item.charCodeAt(0))).toString(16).toUpperCase()
                    if(this.fonts[unicode]){
                        newFontObject[unicode] = this.fonts[unicode]
                        textObj += `<span style="color: red; text-decoration: underline;">${item}</span>`
                    }else{
                        textObj += item
                    }
                })
            this.textObj = textObj
            this.fontList = newFontObject
        },
        initFonts(){
            this.fontList = JSON.parse(JSON.stringify(this.fonts))
            this.textObj = ''
        },
        changeFonts(fontName){
            this.searchFont = ''
            this.formatFonts(fontName)
            this.$nextTick(() => {
                this.fontFamily = `longfor-font-${fontName}`
            })
        },
        realCodeFn(){
            if(!this.realCode){
                this.realCodeText = ''
            }
            let textArr = []
            for(let item = 0; item < this.realCode.length; item += 2){
                textArr.push(this.realCode.slice(item, item + 2))
            }
            this.realCodeText =  textArr
                .map(item => {
                    return parseInt(String(item), 4).toString(16)
                })
                .join('')
        },
        codeFn(){
            if(!this.code){
                this.codeText = ''
            }
            this.codeText = '<span style="color: red; font-size: 30px">3</span>' + this.code
                .split('')
                .map(item => {
                    let num = parseInt('0x' + item).toString(4)
                    return num < 4 ? '0' + num : num
                })
                .join('')
        },
        clearRealCode(){
            this.realCodeText = ''
        },
        clearCode(){
            this.codeText = ''
        },
        initPage(){
            this.fontNames = getFontsList()
            this.radioFont = this.fontNames[0].fontName
        }
    },
    mounted(){
        this.initPage()
        this.formatFonts(this.radioFont)
    }
}
</script>
