#font{
    font-size: 14px;
    display: flex;
    flex-wrap: wrap;
    // justify-content: center;
}
.font-item-list{
    display: flex;
    justify-content: center;
}
.font-item{
    font-size: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // height: 50px;
    width: 80px;
    padding: 30px;
    cursor: pointer;
    span{
        font-size: 25px;
    }
    .font-transfrom{
        transition: all .5s;
        &:hover{
            transform: scale(6);
            // transform:translate3d(0,0,0);
            // font-size: 50px;
            background-color: #fff;
        }
    }
    
}
